<template>
  <div id="stockwarn">
    <div style="text-align: center">
      <h3 style="color: red"></h3>
    </div>
    <div style="position: absolute; top: 20px; left: 20px">
      <el-button type="primary" @click="dialogFormVisible = true"
        ></el-button
      >
      <el-dialog
        title=""
        :visible.sync="dialogFormVisible"
        :modal-append-to-body="false"
      >
        <el-form :model="sizeForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              v-model="sizeForm.num"
              :disabled="true"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              v-model="sizeForm.updatenum"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"
            > </el-button
          >
        </div>
      </el-dialog>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      border
      style="margin: 10px auto; width: 1101px"
      tooltip-effect="dark"
      :header-cell-style="{ background: '#f7f7f7' }"
    >
      <el-table-column
        prop="goodsId"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="goodsName"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="goodsPrice"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="supName"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="goodsStatus"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="typeName"
        label=""
        width="150"
      ></el-table-column>
      <el-table-column
        prop="stockNum"
        label=""
        width="150"
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "stockwarn",
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      currentPage4: "",
      total: 50,
      search: "",
      sizeForm: {
        num: "95",
        updatenum: "",
      },
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      formLabelWidth: "120px",
    };
  },
  created() {
    this.$axios
      .post("http://127.0.0.1:3000/stockwarn", { num: this.sizeForm.num })
      .then((res) => {
        this.tableData = res.data;
      })
      .catch((error) => {
        window.console.log(error);
      });
  },
  methods: {
    onSubmit() {
      alert("ksugck");
    },
  },
};
</script>

<style scoped>
#supplier {
  width: 100%;
}
</style>